import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-schedule',
  templateUrl: './schedule.component.html',
  styleUrls: ['./schedule.component.css']
})
export class ScheduleComponent implements OnInit {
  showSchedNotes: boolean;
  showQ1: boolean;
  showQ2: boolean;
  showQ3: boolean;
  showQ4: boolean;

  months: string[];

  constructor() { 
    this.months = [
      'jan', 'feb', 'mar', 'apr', 'may', 'jun',
      'jul', 'aug', 'sep', 'oct', 'nov', 'dec'
    ]
  }

  ngOnInit() {
    this.showSchedNotes = false;
    this.showQ1 = false;
    this.showQ2 = true;
    this.showQ3 = false;
    this.showQ4 = false;  
  }

  showPageNotes() {
    this.showSchedNotes = !this.showSchedNotes;
  }

  showQtr(showQtr: string) {
    if(showQtr === 'q1') {
      this.showQ1 = !this.showQ1;
    }
    if(showQtr === 'q2') {
      this.showQ2 = !this.showQ2;
    }
    if(showQtr === 'q3') {
      this.showQ3 = !this.showQ3;
    }
    if(showQtr === 'q4') {
      this.showQ4 = !this.showQ4;
    }
  }

}
