import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { HomeComponent } from './home/home.component';
import { NewsComponent } from './news/news.component';
import { ScheduleComponent } from './schedule/schedule.component';
import { ResultsComponent } from './results/results.component';
//import { KdComponent } from './kd/kd.component';
import { PreaknessComponent } from './preakness/preakness.component';
import { SaratogaComponent } from './components/saratoga/saratoga.component';

import { AboutComponent } from './about/about.component';
import { NotfoundComponent } from './components/notfound/notfound.component';

const routes: Routes = [
  {path: '', component: HomeComponent},
  {path: 'home', component: HomeComponent},
  {path: 'news', component: NewsComponent},
  {path: 'schedule', component: ScheduleComponent},
  {path: 'results', component: ResultsComponent},
  //{path: 'kd', component: KdComponent},
  {path: 'preakness', component: PreaknessComponent},
  {path: 'saratoga', component: SaratogaComponent},
  {path: 'about', component: AboutComponent},
  {path: '**', component: NotfoundComponent}
];


@NgModule({
  exports: [RouterModule],
  imports: [
    RouterModule.forRoot(routes)
  ], 
  declarations: []
})
export class AppRoutingModule { }
