import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-kd',
  templateUrl: './kd.component.html',
  styleUrls: ['./kd.component.css']
})
export class KdComponent implements OnInit {
  showLinks: boolean;
  showRecap: boolean;
  showPreview: boolean;

  constructor() { }

  ngOnInit() {
    this.showLinks = true;
    this.showRecap = false;
    this.showPreview = false;
  }

  showKdDiv(showKdDiv: string) {
    this.showLinks = false;
    this.showRecap = false;
    this.showPreview = false;

    if(showKdDiv === 'showLinks') {
      this.showLinks = true;
    }
    if(showKdDiv === 'showRecap') {
      this.showRecap = true;
    }
    if(showKdDiv === 'showPreview') {
      this.showPreview = true;
    }
    
  }

}
