import { Component, OnInit } from '@angular/core';
import { KdHorsesService } from '../../services/kd-horses.service';
import { kdhorse } from 'src/app/models/kdhorse';

@Component({
  selector: 'app-kd-ponies',
  templateUrl: './kd-ponies.component.html',
  styleUrls: ['./kd-ponies.component.css']
})
export class KdPoniesComponent implements OnInit {
  kdHorseList: kdhorse[];

  constructor(private horseListService: KdHorsesService) { }

  ngOnInit() {
    this.kdHorseList = this.horseListService.getKDHorses();
  }

  postPositionList() {
    this.kdHorseList = this.horseListService.getKDHorses();
  }

  rankedPositionList() {
    this.kdHorseList = this.horseListService.getRankedKDHorses();
  }

  pointsPositionList() {
    this.kdHorseList = this.horseListService.getPointsKDHorses();
  }
}
