import { Component, OnInit } from '@angular/core';
import { PreakHorsesService } from 'src/app/services/preak-horses.service';
import { horse } from 'src/app/models/horse';

@Component({
  selector: 'app-preak-ponies',
  templateUrl: './preak-ponies.component.html',
  styleUrls: ['./preak-ponies.component.css']
})
export class PreakPoniesComponent implements OnInit {
  preakHorseList: horse[];

  constructor(private horseListService: PreakHorsesService) { }

  ngOnInit() {
    this.preakHorseList = this.horseListService.getPreaknessHorses();
  }

  postPositionList() {
    this.preakHorseList = this.horseListService.getPreaknessHorses();
  }

  rankedPositionList() {
    this.preakHorseList = this.horseListService.getRankedPreaknessHorses();
  }

  pointsPositionList() {
    this.preakHorseList = this.horseListService.getPointsPreaknessHorses();
  }
}
